<template lang="html">
  <div class="area-check-block-box">
    <div class="check-item" @click.stop="isShow = !isShow">
      <check-list :quarter="areas" v-model="chooseArea"></check-list>
    </div>
    <div v-show="isShow" class="check-sub-item">
      <check-list @change="changeItemCheck" :quarter="subAreas" v-model="item.regionIds"></check-list>
    </div>
  </div>
</template>

<script>
import checkList from '@/components/minCommon/checkList'
export default {
  components: {checkList},
  props: {
    item: Object,
    index: Number
  },
  data () {
    return {
      isShow: false,
      chooseArea: this.item.choseArea
    }
  },
  computed: {
    areas () {
      this.item.label = this.item.area
      this.item.value = this.item.id
      return [this.item]
    },
    subAreas () {
      return this.item.dataRegions.map(item => {
        item.label = item.regions
        item.value = item.id
        return item
      })
    }
  },
  created () {
    if (this.chooseArea) this.item.regionIds = this.item.dataRegions.map(item => item.id)
  },
  watch: {
    chooseArea: {
      handler (val) {
        if (val && val.length > 0) {
          this.item.regionIds = this.item.dataRegions.map(item => item.id)
        } else {
          this.item.regionIds = []
        }
        this.$emit('submit-area', this.item, this.index)
      },
      deep: true
    }
  },
  methods: {
    changeItemCheck (area) {
      this.curItem.regionIds = area
      this.$emit('submit-area', this.item, this.index)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.check-sub-item {
  padding-left: .6rem;
}
</style>



