<template lang="html">
  <div>
    <default-head header="地域推广" :path="path"></default-head>
    <div class="check-box" id="wrapper">
      <check-block v-for="(item, index) in areas"
       :key="index" @submit-area="getAreas" :item="item" :index="index"></check-block>
    </div>
    <div class="area-foot-btn">
      <sub-button class="area-foot-btn" @click.native="sumitArea"></sub-button>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import checkBlock from '@/components/area/checkBlock'
import subButton from '@/components/common/subButton'

export default {
  data: function () {
    return {
      areas: [],
      regions: {},
      userArea: [],
      sign: this.$route.query.sign
    }
  },
  components: { checkBlock, defaultHead, subButton },
  created () {
    this.initPage()
  },
  computed: {
    userId () {
      return utils.userId()
    },
    path () {
      let sign = this.$route.query.sign
      return sign === 'company' ? '/companyinfo' : '/personinfo'
    }
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      this.ajaxGetArea()
    },
    sumitArea () {
      let dataArr = []
      this.areas.forEach(item => {
        if (item.regionIds && item.regionIds.length > 0) {
          dataArr.push({areaId: item.id, regionIds: item.regionIds})
        }
      })
      if (dataArr.length <= 0) {
        minutils.Toast({ message: '推广区域不能为空', position: 'top' })
        return
      }
      let data = {userId: this.userId, data: dataArr}
      this.ajaxSaveUserArea(data)
    },
    getAreas (item, index) {
      this.areas[index] = item
    },
    ajaxGetArea () {
      minutils.IndicatorOpen('加载中...')
      api.getAllArea().then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.ajaxGetUserArea({userId: this.userId, style: this.sign === 'company' ? 2 : 1}, result.data)
        }
      })
    },
    ajaxGetUserArea (data, areas) {
      api.getUserArea(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.userArea = result.data
          areas.forEach((item, index) => {
            result.data.forEach((item1, index1) => {
              if (item.id === item1.areaId) {
                let data = {...item, ...item1}
                // 判断是否选中区域
                if (data.dataRegions.length <= data.regionIds.length) {
                  data.choseArea = [data.id]
                }
                areas[index] = data
              }
            })
          })
          this.areas = areas
        }
      }) // end of ajax
    },
    ajaxSaveUserArea (data) {
      api.saveUserArea(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          let self = this
          minutils.MessageBox({
            message: '保存区域信息成功',
            callback () {
              if (self.sign === 'person') {
                sessionStorage.setItem('personArea', JSON.stringify(data.data))
                self.$router.push('/personinfo')
              } else {
                sessionStorage.setItem('companyArea', JSON.stringify(data.data))
                self.$router.push('/companyinfo')
              }
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';
.check-box {
  background:@whiteColor;
}
.area-foot-btn {
  margin-top: .6rem;
}
</style>



